li.project {
    a {
        text-decoration: none;
        color: inherit;
    }
    .project-summary {
        display: flex;
        flex-direction: row;
        align-items: start;
        margin-bottom: 20px; /* Space between project summaries */
        border: 1px solid transparent;
        padding: 0.5em;
        border-radius: 0.5em;
        justify-content: space-between;

        &:hover {
            border: 1px solid silver;
        }
    
        .project-thumbnail {
            margin-right: 1em; /* Space between image and text on left */
            margin-left: 0; /* Space between image and text on right */
            max-width: 100px; /* Set maximum width for images */
            height: auto; /* Maintain aspect ratio */
            border-radius: 15px;
        }
    
        .project-details { 
            display: flex;
            flex-direction: column; /* Stack title and description vertically */
            justify-content: center; /* Center vertically if needed */
    
            h2, p {
                margin: 0; /* Remove default margin */
            }           

        }
    }

    &:nth-child(even) a .project-summary {
        background-color: lighten(#212121, 10%);
        // flex-direction: row-reverse;
        // justify-content: flex-end;

        // .project-thumbnail {
        // }
    }
}